body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Source Code Pro', monospace;
}


.NavbarItems{
    background-color:#A5E3E3;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}

.navbar-logo{
    color:white;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}

.fa-cash-register{
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu{
    display:grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
    
}

.nav-links{
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover{
    background-color: #B6F2F7;
    border-radius: 4px;
    transition: all 0.2s ease-out;
    color: #09079F;
}

.fa-bars{
    color: rgb(151, 60, 60);
}

.nav-links-mobile{
    display: none;
}

.menu-icon{
    display: none;
}

a:hover, a:visited, a:link, a:active
{
    text-decoration: none;
}
